.feedback__form {
  box-sizing: border-box;
  margin: 0;
  width: 320px;
}

@media (min-width: @tablet-width) {
  .feedback__form {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    padding-left: 60px;
    padding-right: 60px;
    padding-bottom: 48px;
    display: grid;
    grid-template-columns: repeat(2, 324px);
    width: 768px;
  }
}

@media (min-width: @desktop-width) {
  .feedback__form {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    padding-left: 120px;
    padding-right: 120px;
    padding-bottom: 64px;
    display: grid;
    grid-template-columns: repeat(2, 480px);
    width: 1200px;
  }
}


@media (min-width: @tablet-width) {
  .feedback__wrapper--line {
    grid-column: 1/-1;

  }
}


@media (min-width: @desktop-width) {
  .feedback__wrapper--line {
    grid-column: 1/-1;
  }
}



.feedback__field {
  border: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}

.feedback__wrapper {
  width: 320px;
  box-sizing: border-box;
  padding: 24px 20px 39px 20px;
  margin: 0;
}

@media (max-width: @mobile-width-only) {
  .feedback__wrapper {
    border-bottom: solid 1px @grey-button-hover;
  }
}

@media (min-width: @tablet-width) {
  .feedback__field {
    box-sizing: border-box;
    border: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    width: 324px;
  }
}

@media (min-width: @desktop-width) {
  .feedback__field {
    box-sizing: border-box;
    border: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    width: 480px;
  }
}

@media (min-width: @tablet-width) {
  .feedback__wrapper--right {
    position: relative;
    width: 324px;
  }

  .feedback__wrapper--right::after {
    position: absolute;
    top: 20px;
    right: -20px;
    height: 338px;
    width: 1px;
    background-color: @form-border;
    content: "";
    z-index: 2;
  }
}

@media (min-width: @desktop-width) {
  .feedback__wrapper--right {
    position: relative;
    width: 480px;
  }

  .feedback__wrapper--right::after {
    position: absolute;
    top: 20px;
    right: -25px;
    height: 338px;
    width: 1px;
    background-color: @form-border;
    content: "";
    z-index: 2;
  }
}


@media (min-width: @tablet-width) {
  .feedback__field--choose {
    position: relative;
  }

  .feedback__field--choose::after {
    position: absolute;
    right: 0;
    top: -43px;
    height: 1px;
    width: 123px;
    background-color: @form-border;
    content: "";
    z-index: 2;
  }

  .feedback__field--choose::before {
    position: absolute;
    left: 0;
    top: -43px;
    height: 1px;
    width: 123px;
    background-color: @form-border;
    content: "";
    z-index: 2;
  }
}


@media (min-width: @desktop-width) {
  .feedback__field--choose {
    position: relative;
  }

  .feedback__field--choose::after {
    position: absolute;
    right: 0;
    top: -43px;
    height: 1px;
    width: 280px;
    background-color: @form-border;
    content: "";
    z-index: 2;
  }

  .feedback__field--choose::before {
    position: absolute;
    left: 0;
    top: -43px;
    height: 1px;
    width: 280px;
    background-color: @form-border;
    content: "";
    z-index: 2;
  }
}

.feedback__button {
  width: 58px;
  height: 58px;
  border: none;
  background-color: @grey-button;
  margin: 0;
  padding: 20px;
  outline: none;
}

.feedback__button--phone {
  background-image: url("/img/icons/phone.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.feedback__button--email {
  background-image: url("/img/icons/mail.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.feedback__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

@media (min-width: @tablet-width) {
  .feedback__list--opinion {
    display: grid;
    grid-template-columns: repeat(2, 280px);
    grid-gap: 16px;
    padding-left: 36px;
    padding-right: 36px;
  }

  .feedback__list--sites {
    display: grid;
    grid-template-columns: repeat(2, 224px);
    grid-column-gap: 32px;
    padding-left: 84px;
    padding-right: 84px;
  }
}

@media (min-width: @desktop-width) {
  .feedback__list--opinion {
    display: grid;
    grid-template-columns: repeat(3, 280px);
    grid-gap: 28px;
    padding-left: 53px;
    padding-right: 53px;
  }

  .feedback__list--sites {
    display: grid;
    grid-template-columns: repeat(4, 172px);
    grid-column-gap: 64px;
    padding-left: 44px;
    padding-right: 44px;
  }
}

.feedback__choice {
  margin-bottom: 16px;
}

.feedback__form-label {
  .text-block__description();
}

@media (min-width: @tablet-width) {
  .feedback__form-label--contacts {
    margin-left: 44px;
  }
}


@media (min-width: @desktop-width) {
  .feedback__form-label--contacts {
    margin-left: 64px;
  }
}

.feedback__title {
  .text-block__title;
  margin-bottom: 20px;
  margin-top: 24px;
  display: block;
}

@media (min-width: @tablet-width) {
  .feedback__title {
    font-size: 21px;
    margin-bottom: 28px;
  }
}


@media (min-width: @desktop-width) {
  .feedback__title {
    font-size: 21px;
    margin-bottom: 28px;
  }
}

.feedback__text {
  font-family: "PT Sans", sans-serif;
  font-size: 12px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.6);
  text-align: right;
  line-height: 21px;
  text-transform: uppercase;
  margin: 0;
  padding-left: 143px;
}

@media (min-width: @tablet-width) {
  .feedback__text {
    padding-top: 23px;
    padding-left: 182px;
  }
}

@media (min-width: @desktop-width) {
  .feedback__text {
    padding-top: 23px;
    padding-left: 247px;
  }
}

.feedback__input {
  box-sizing: border-box;
  width: 280px;
  height: 58px;
  margin-bottom: 16px;
  background-color: @grey-input;
  border: none;
  padding: 16px;
}

@media (min-width: @desktop-width) {
  .feedback__input {
    width: 430px;
  }
}


.feedback__input:hover {
  background-color: @grey-button-hover;
}

.feedback__input:active {
  background-color: @white;
  outline: solid 4px @grey-button-hover;
}

@media (min-width: @tablet-width) {
  .feedback__input--name {
    margin-right: 43px;
  }
}

@media (min-width: @desktop-width) {
  .feedback__input--name {
    margin-right: 63px;
  }
}

.feedback__input--contacts {
  box-sizing: border-box;
  width: 222px;
  margin-right: 0;
}

@media (min-width: @tablet-width) {
  .feedback__input--contacts {
    margin-left: 44px;
  }
}

@media (min-width: @desktop-width) {
  .feedback__input--contacts {
    width: 344px;
    margin-left: 64px;
  }
}

.feedback__input::placeholder {
  font-family: "PT Sans", sans-serif;
  font-size: 18px;
  font-weight: 700;
  color: @black;
  text-align: left;
  line-height: 26px;
}

.feedback__input--contacts::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.feedback__input--contacts::-moz-placeholder,
.feedback__textarea::-moz-placeholder {
  color: @black;
  opacity: 0.3;
}


@media (min-width: @tablet-width) {
  .feedback__field--textarea {
    box-sizing: border-box;
    padding: 0;
    width: 648px;
  }
}

@media (min-width: @desktop-width) {
  .feedback__field--textarea {
    box-sizing: border-box;
    padding-left: 96px;
    padding-right: 96px;
    width: 768px;
  }
}

.feedback__textarea {
  .text-block__title();
  border: none;
  width: 279px;
  min-height: 150px;
  background-color: @grey-input;
  padding: 16px;
  box-sizing: border-box;
  margin-bottom: 56px;
  color: rgba(0, 0, 0, 0.3);
  text-align: left;
  text-transform: none;
}

@media (min-width: @tablet-width) {
  .feedback__textarea {
    width: 648px;
    min-height: 150px;
    margin-bottom: 48px;
  }
}

@media (min-width: @desktop-width) {
  .feedback__textarea {
    width: 768px;
    min-height: 160px;
  }
}

.feedback__button--blue {
  width: 281px;
  padding: 18px 33px 14px 33px;
  margin-bottom: 0;
}


@media (min-width: @tablet-width) {
  .feedback__button--blue {
    width: 324px;
    padding: 18px 49px 14px 49px;
    margin-bottom: 0;
  }
}

@media (min-width: @desktop-width) {
  .feedback__button--blue {
    width: 384px;
    padding: 18px 58px 14px 58px;
    margin-bottom: 0;
  }
}

.feedback__control {
  position: relative;
  display: block;
  padding-left: 39px;
  margin-bottom: 16px;
}

.feedback__control:last-child {
  margin-bottom: 0;
}

.feedback__mark {
  position: absolute;
  top: 1px;
  left: 0;
  width: 46px;
  height: 46px;
  border-radius: 4px;
  background-color: @grey-input;
  margin-bottom: 16px;
}

.feedback__control:hover .feedback__mark {
  background-color: @grey-button-hover;
}

.feedback__control:focus-within .feedback__mark {
  background-color: @grey-button-hover;
}

.feedback__control:active .feedback__mark {
  background-color: @grey-button-active;
}

.feedback__mark--circle {
  border-radius: 50%;
}

.feedback__control--cirlce:focus-within .feedback__mark--circle {
  background-color: @grey-button-hover;
}

.feedback__control--cirlce:hover .feedback__mark--circle {
  background-color: @grey-checked;
}

.feedback__control--cirlce:active .feedback__mark--circle {
  background-color: rgba(51, 51, 51, 0.3);
}

.feedback__choice[type="checkbox"]:checked+.feedback__mark::after {
  position: absolute;
  top: 15px;
  left: 12px;
  width: 22px;
  height: 16px;
  background-image: url("../img/icons/tick.svg");
  content: "";
}

.feedback__choice[type="radio"]:checked+.feedback__mark--circle::before {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: @grey-checked;
  content: "";
}


.feedback__control-label {
  .text-block__title();
  display: block;
  text-align: left;
  text-transform: none;
  padding-left: 16px;
  padding-top: 10px;
}

.feedback__control-label:last-child {
  margin-bottom: 0;
}


.feedback__item--choice {
  margin-bottom: 16px;
  width: 280px;
  height: 46px;
}
